h1{
	text-align: center;
	font-size: 2.5em;
  padding-top: 0.5em;
  font-family: fantasy, sans-serif;
}
p{
  text-align: center;
  font-size: 1em;
}
h2{
  font-family: fantasy;
}
#leaderboard{
  position: absolute;
  width: 10%;
  top: 10em;
  right: 10em;
}
th{
  background-color: #dddddd;
}
table{
  font-family: arial, sans-serif;
  border-collapse: collapse;
}
td,th{
  text-align: left;
  padding: 0.5em;
}
tr:nth-child(odd){
  background-color: #dddddd;
}
.player-name{
  max-width: 10em;
  /* text-overflow: ellipsis; */
}
.player-name-span{
  max-width: 10em;
  text-overflow: ellipsis;
}
.control{
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu{
  display: flex;
  width: 30em;
  justify-content: space-between;
  margin: 0.2em 0;
}
.switch{
  display: flex;
  width: 12.5em;
  justify-content: space-between;
  align-items: center;
}
.modal-background{
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}
.content-box{
  background-color: #FAEDF0;
  margin: 15em auto;
  height: 10em;
  border-radius: 0.3em;
  width: 20em;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.74);
  text-align: center;
}
.congrats{
  font-size: 1.2em;
  padding-top: 2em;
  text-align: center;
}
.special-congrats{
  padding-top: 1.3em;
}
#submit-name{
  width: 15em;
  margin: 0 auto;
  margin-top: -0.6em;
  margin-bottom: 0.5em;
}
#turn{
  width: 17em;
  font-size: 1.2em;
  font-family: monospace;
  padding-top: 1em;
  line-height: 0.8em;
}
#warning{
  color: red;
  line-height: 0.2em;
}
.info-box{
  display: flex;
  flex-direction: column;
  height: 4em;
}
.btn-text{
  font-family: Arial, Helvetica, sans-serif;
}
.close {
  color: #aaa;
  float: right;
  margin-right: 0.2em;
  margin-top: -0.1em;
  font-size: 1.8em;
  font-weight: bold;
  width: 1.5em;
  text-align: center;
}
.close:hover, .close:focus{
  color: black;
  text-decoration: none;
  cursor: pointer;
}
#score-board{
  display: flex;
  margin: auto;
  width: 30em;
  justify-content: space-around;
  align-items: center;
  padding: 0.6em;
}
#board{
  margin: auto;
  background-color: wheat;
  width: 32em;
  height: 32em;
  display: flex;
  flex-wrap: wrap;
}
.tile{
  width: 4em;
  height: 4em;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.tile-black{
  background-color: #292C6D;
}
.tile-white{
  background-color: #FAEDF0;
}
.piece {
  display: flex;
  width: 3em;
  height: 3em;
  margin: 0;
  border-radius: 50%;
  position: relative;
  justify-content: center;
  align-items: center;
}
.potential {
  display: flex;
  width: 3em;
  height: 3em;
  margin: 0;
  border-radius: 50%;
  position: relative;
  justify-content: center;
  align-items: center;
}
.potential-black{
  box-shadow: 0 0 0.5em pink;
}
.potential-white{
  box-shadow: 0 0 0.5em rgb(255, 124, 124);
}
.piece:before {
  content: "";
  position: absolute;
  background: radial-gradient(circle at 50% 120%, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0) 70%);
  border-radius: 50%;
  bottom: 2.5%;
  left: 5%;
  opacity: 0.6;
  height: 100%;
  width: 90%;
  filter: blur(0.4em);
  z-index: 2;
}
.piece:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 160%;
  left: 150%;
  border-radius: 50%;
  background: radial-gradient(circle at 50% 50%, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8) 14%, rgba(255, 255, 255, 0) 24%);
  transform: translateX(-5em) translateY(-5.6em) skewX(-20deg);
  filter: blur(0.15em);
}
.piece-black{
  background: radial-gradient(circle at 50% 120%, #323232, #0a0a0a 80%, #000000 100%);
  box-shadow: 0 0 0.5em #FAEDF0;
  color: white;
}
.piece-white{
  background: radial-gradient(circle at 50% 120%, #e0e0e0, #ececec 80%, white 100%);
  box-shadow: 0 0 0.5em #292C6D;
}